<template>
    <div>
        <hader/>
        <div>
            <div class ="banner">
                <!-- <div class="tit">[ 让每一个商户都成为我们的朋友 ]</div>
                <div class="cont">
                    <span>网址：www.dabojituan.com </span>
                    <span> 邮箱：dbzhpt@gmail.com</span>
                    <div>接待时间：7x24 小时</div>
                </div> -->
            </div>            
            <div class="productCont">
              <div class="tit">案例展示</div>
              <div class="list">
                  <div v-for="item in list" :key="item.id">
                    <img :src="item.img" alt="">
                  </div>
              </div>  
            </div>
        </div> 
        <footera/>
        <div class="nav row">
            <div :class="{active:nav == 'index'}" @click="navClick('index')">
                <div>
                    <img :src="nav == 'index'?img.indexa:img.index" >
                </div>
                <div>
                    <p>首页</p>
                </div>
            </div>
            <div :class="{active:nav == 'product'}" @click="navClick('product')">
                <div>
                    <img :src="nav == 'product'?img.producta:img.product">
                </div>
                <div>
                    <p>产品</p>
                </div>
            </div>
            <div :class="{active:nav == 'advantage'}" @click="navClick('advantage')"> 
                <div>
                    <img :src="nav == 'advantage'?img.advantagea:img.advantage">
                </div>
                <div>
                    <p>优势</p>
                </div>
            </div>
            <div :class="{active:nav == 'partner'}" @click="navClick('partner')">
                <div>
                    <img :src="nav == 'partner'?img.partnera:img.partner">
                </div>
                <div>
                    <p>伙伴</p>
                </div>
            </div>
            <div :class="{active:nav == 'customized'}" @click="navClick('customized')">
                <div>
                    <img :src="nav == 'customized'?img.creationa:img.creation">
                </div>
                <div>
                    <p>定制</p>
                </div>  
            </div>
            <div :class="{active:nav == 'about'}" @click="navClick('about')">
                <div>   
                    <img :src="nav == 'about'?img.abouta:img.about">
                </div>
                <div>
                    <p>关于</p>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import hader from '@/components/header';
import footera from '@/components/footer';
export default {
    name:'product',
    components:{
        hader,footera
    },
    data(){
        return{
            list:[
                {
                    img:require('@/assets/st-imges/case-1@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-2@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-3@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-4@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-5@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-6@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-7@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-8@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-9@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-10@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-11@2x.png')
                },
                {
                    img:require('@/assets/st-imges/case-12@2x.png')
                }
            ],
            data:'',
            nav:'product',
            img:{
                index:require('@/assets/st-imges/index.png'),
                indexa:require('@/assets/st-imges/indexa.png'),
                about:require('@/assets/st-imges/about.png'),
                abouta:require('@/assets/st-imges/abouta.png'),
                creation:require('@/assets/st-imges/creation.png'),
                creationa:require('@/assets/st-imges/creationa.png'),
                advantage:require('@/assets/st-imges/advantage.png'),
                advantagea:require('@/assets/st-imges/advantagea.png'),
                partner:require('@/assets/st-imges/partner.png'),
                partnera:require('@/assets/st-imges/partnera.png'),
                product:require('@/assets/st-imges/product.png'),
                producta:require('@/assets/st-imges/producta.png'),
            }
        }
    },
    methods:{
        navClick(str){
            // this.nav = str
            if(str == this.nav ) return false;
            let path = str;
            if(str == 'index'){
                path ='/'
            }
            this.$router.push({ path: path });
        }
    }
}
</script>
<style scoped>
    .banner{
        background: url("./../../assets/st-imges/partnerBanner@2x.png") no-repeat;
        background-size: 100% 100%;
        background-color:rgba(20,10,3,1); 
        /* height: 1.3rem; */
        color: #fff;
        height: 2.3rem;
        padding: 0.54rem 0.5rem 0.64rem;
    }
    .banner .tit{
        text-align: center;
        font-size: 0.36rem;
        margin-bottom: 0.12rem;
    }
    .banner .cont{
        text-align: center;
        font-size: 0.26rem;
        line-height: 0.4rem;
    }  
    .productCont{
        margin: 0 0.3rem;
        text-align: center;
    }
    .productCont .tit{
        margin: 0.4rem 0 0.44rem;
        position: relative;
        font-size: 0.34rem;
        color: #555555;
        font-weight:bold;
    }
    .productCont .tit::after{
        display: inline-block;
        content: '';
        width: 0.6rem;
        height: 0.02rem;
        background: #E63440;
        position: absolute;
        bottom: -0.1rem;
        left: 45%;
    }
    .list{
        display: flex;
        justify-content:space-between;
        /* align-items: center; */
        flex-wrap:wrap;
    }
    .list div{
        width: 50%;
        height: 2.7rem;
        margin-bottom: 0.2rem;
    }
    .list div img{
        width: 100%;
        height: 100%;
    }
/* 导航 */
    .nav{
        position: fixed;
        bottom: 0;
        left: 0;
        background: #fff;
        height: 1rem;
        width: 100%;
    }
    .nav>div{
        width: 16%;
        height: 100%;
    }
    .nav>div>div{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        height: 50%;
    }
    .nav>div img{
        width: .34rem;
        height: .34rem;
    }
    /* .list{
        width: 100%;
        height: 100%;
        position: absolute;
        overflow-x: hidden;
        overflow-y: auto;
    } */
    .active{
        color: #E63440;
    }        
</style>


